<template>
  <div>
    <b-button @click="showModal">+ Add Package</b-button>
    <b-modal
      size="xl"
      variant="dark"
      header-bg-variant="dark"
      header-text-variant="light"
      content-class="border-dark"
      v-model="isShowModal"
    >
      <!-- header -->
      <template v-slot:modal-header="{ close }">
        <b-button size="sm" @click="cancel()">x</b-button>
      </template>

      <!-- content -->
      <b-tabs v-model="tabIndex">
        <b-tab title="템플릿으로 패키지 생성" active>
          <div v-if="template === null">
            <div
              v-for="(item, idx) in rosTemplate"
              :key="idx"
            >
            <b-row>
              <b-col>
                <b-card>
                  <span class="text-info" @click="applyTemplate(item.metadata.name)">{{item.metadata.name}}</span>
                </b-card>
              </b-col>
            </b-row>
            </div>
          </div>
          <div v-else>
            <div
              v-for="(param, idx) in paramForm"
              :key="idx"
              >
              <b-form-group
                class="mb-0"
                :label="param.displayName"
                :description="param.description"
                style="font-weight: bold;"
              >
                <b-form-input
                  v-model="param.text"
                  :placeholder="param.value"
                ></b-form-input>
              </b-form-group>
              <br>
            </div>
          </div>
        </b-tab>
        <b-tab title="빌드된 이미지 올리기">
          bbb
        </b-tab>
      </b-tabs>

      <!-- footer -->
      <template v-slot:modal-footer>
        <b-button
          variant="dark"
          @click="cancel()"
        >
          Cancel
        </b-button>
        <b-button
          type="submit"
          variant="primary"
          :disabled="!checkForm"
          @click="doCreate()"
        >
          Create
        </b-button>
      </template>
    </b-modal>

    <!-- error message -->
    <one-button-alert name="errorModal" ref="errorModal" />
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import OneButtonAlert from '../../components/modal/OneButtonAlert.vue'

export default {
  name: 'addPackage',
  components: {
    OneButtonAlert
  },
  props: {
    projectName: {
      type: String,
      default: () => ''
    }
  },
  data () {
    return {
      isShowModal: false,
      tabIndex: 0,
      template: null,
      paramForm: []
    }
  },
  computed: {
    ...mapState('template', ['templateList']),
    ...mapState('storage', ['storageList']),
    rosTemplate () {
      return this.templateList.items.filter(item => {
        if (item.labels && item.labels.template) {
          return item.labels.template.includes('ros2')
        }
        return false
      })
    },
    checkForm () {
      if (this.paramForm.length === 0) {
        return false
      }
      for (var i = 0; i < this.paramForm.length; i++) {
        if (this.paramForm[i].required && !this.paramForm[i].text) {
          return false
        }
      }
      return true
    }
  },
  methods: {
    ...mapActions('template', ['getTemplateList', 'getTemplate']),
    ...mapActions('build', ['createBuildConfig']),
    ...mapActions('deploy', ['createDeployConfig']),
    ...mapActions('imagestream', ['createImageStream']),
    showModal () {
      this.getTemplateList()
      this.isShowModal = true
    },
    applyTemplate (name) {
      this.getTemplate(name).then((t) => {
        this.template = t
        t.parameters.forEach(p => {
          let newValue = p.value
          switch (p.name) {
            case 'MOUNT_PATH':
              newValue = '/data'
              break
            case 'VOLUME_NAME':
              newValue = 'volume-' + Math.random().toString(36).substring(5)
              break
            case 'STORAGE_NAME':
              let storage = this.storageList.get(this.projectName)
              if (storage) {
                newValue = storage.items[0].metadata.name
              }
              break
            default:
              break
          }
          this.paramForm.push({
            name: p.name,
            displayName: (p.required === true ? '* ' + p.displayName : p.displayName),
            description: p.description,
            value: newValue,
            required: p.required,
            text: newValue
          })
        })
      })
    },
    cancel () {
      this.isShowModal = false
      this.resetTab()
    },
    doCreate () {
      this.applyObjects()
      this.isShowModal = false
      this.resetTab()
    },
    resetTab () {
      this.template = null
      this.paramForm = []
    },
    replaceAll (str, searchStr, replaceStr) {
      return str.split(searchStr).join(replaceStr)
    },
    errorHandling (kind, object) {
      switch (kind) {
        case 'ImageStream':
        case 'BuildConfig':
        case 'DeploymentConfig':
        default: break
      }
    },
    applyObjects () {
      let objects = JSON.stringify(this.template.objects)
      this.paramForm.forEach(p => {
        if (p.text) {
          objects = this.replaceAll(objects, '${' + p.name + '}', p.text)
        }
      })
      objects = JSON.parse(objects)
      objects.forEach(o => {
        switch (o.kind) {
          case 'ImageStream':
            this.createImageStream({
              projectName: this.projectName,
              form: o
            })
              .catch(error => {
                this.errorHandling(o.kind, o)
                this.$refs.errorModal.show({
                  title: '에러 메세지',
                  text: error
                })
              })
            break
          case 'BuildConfig':
            this.createBuildConfig({
              projectName: this.projectName,
              form: o
            })
              .then(response => {
                this.errorHandling(o.kind, o)
                this.$refs.errorModal.show({
                  title: '메세지',
                  text: '패키지 생성 성공'
                })
              })
              .catch(error => {
                this.$refs.errorModal.show({
                  title: '에러 메세지',
                  text: error
                })
              })
            break
          case 'DeploymentConfig':
            this.createDeployConfig({
              projectName: this.projectName,
              form: o
            })
              .catch(error => {
                this.errorHandling(o.kind, o)
                this.$refs.errorModal.show({
                  title: '에러 메세지',
                  text: error
                })
              })
            break
          default:
            break
        }
      })
    }
  },
  watch: {
    tabIndex (idx) {
      if (this.tabIndex !== idx) {
        this.resetTab()
      }
    }
  }
}
</script>

