<template>
  <b-container fluid>
    <b-row>
      <div class="col text-left">
        <b-form-group
          label="Filter"
          label-cols-sm="3"
          label-align-sm="left"
          label-size="sm"
          label-for="filterInput"
          class="mb-0"
        >
          <b-input-group size="sm">
            <b-form-input
              v-model="filter"
              type="search"
              id="filterInput"
              placeholder="Filter by name"
            ></b-form-input>
            <b-input-group-append>
              <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </div>
      <div class="col text-right">
        <add-package :project-name="projectName"></add-package>
      </div>
    </b-row>
    <b-row>
      <b-card>
        <b-table
          :hover="true"
          :bordered="true"
          :small="small"
          :fixed="fixed"
          responsive="sm"
          :items="tableData"
          :fields="fields"
          :filter="filter">
          <!-- complex header -->
          <template v-slot:thead-top="data">
            <b-tr>
              <b-th></b-th>
              <b-th colspan="2">Build</b-th>
              <b-th colspan="2">Deployment</b-th>
              <b-th></b-th>
              <b-th></b-th>
              <b-th></b-th>
            </b-tr>
          </template>
          <!-- package name row -->
          <template v-slot:cell(name)="row">
            <span class="text-info" @click="goLink(row.item)">{{row.item.name}}</span>
          </template>
          <template v-slot:cell(bHistory)="row">
            <span :class="{ 'text-danger' : onHighlight(row.item.bHistory) }">
              {{row.item.bHistory}}
            </span>
          </template>
          <template v-slot:cell(dHistory)="row">
            <span :class="{ 'text-danger' : onHighlight(row.item.dHistory) }">
              {{row.item.dHistory}}
            </span>
          </template>
          <template v-slot:cell(status)="row">
            {{getStatus(row.item)}}
          </template>
          <template v-slot:cell(action)="row">
            <div v-if="getStatus(row.item) === '구동중'">
              <b-button @click="stopPod(row.item)">정지</b-button>
            </div>
            <div v-else-if="getStatus(row.item) === '구동가능'">
              <b-button @click="startPod(row.item)">시작</b-button>
              <b-button @click="deletePackage(row.item)">삭제</b-button>
            </div>
            <div v-else-if="getStatus(row.item) === '구동불가능'">
              <b-button @click="deletePackage(row.item)">삭제</b-button>
            </div>
          </template>
        </b-table>
      </b-card>
    </b-row>
    <one-button-alert name="packageError" ref="packageError" />
  </b-container>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import OneButtonAlert from '../components/modal/OneButtonAlert.vue'
import AddPackage from '../components/modal/AddPackage.vue'
// eslint-disable-next-line no-unused-vars
import { queryBuild, queryDeployConfig, queryController, queryPod, queryPodForBuild } from '../utils/Query'
import { timeSince } from '../utils/Date'

export default {
  name: 'packages',
  components: {
    OneButtonAlert,
    AddPackage
  },
  inheritAttrs: false,
  props: {
    caption: {
      type: String,
      default: 'Table'
    },
    hover: {
      type: Boolean,
      default: false
    },
    striped: {
      type: Boolean,
      default: false
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    perPage: {
      type: Number,
      default: 100
    },
    dark: {
      type: Boolean,
      default: false
    },
    projectName: {
      type: String,
      default: () => this.currentProject
    }
  },
  data () {
    return {
      fields: [
        { key: 'name', label: 'Name' },
        { key: 'bNumber', label: 'Last Build' },
        { key: 'bHistory', label: 'Status(History)' },
        { key: 'dVersion', label: 'Last Version' },
        { key: 'dHistory', label: 'Status(History)' },
        { key: 'dAge', label: 'Age' },
        { key: 'status', label: 'Status' },
        { key: 'action', label: 'Action' }
      ],
      filter: null
    }
  },
  computed: {
    ...mapState('project', ['currentProject']),
    ...mapState('build', ['buildConfigs', 'buildList', 'getBuildCount']),
    ...mapState('deploy', ['deployConfigs']),
    ...mapState('pod', ['podList', 'getPodCount']),
    ...mapState('controller', ['controllerList']),
    tableData () {
      let dataset = []
      this.buildConfigs.items.forEach(bconfig => {
        let data = {
          name: '-',
          bNumber: '-',
          bHistory: '-',
          dVersion: '-',
          dHistory: '-',
          dAge: '-',
          status: '-',
          action: '-'
        }
        data.name = bconfig.metadata.name
        data.bNumber = '#' + bconfig.status.lastVersion
        let build = queryBuild(this.buildList, bconfig.metadata.name, bconfig.status.lastVersion)
        if (build) {
          data.bHistory = build.status.phase
        }
        let dConfig = queryDeployConfig(this.deployConfigs, bconfig.metadata.name)
        if (dConfig) {
          data.dVersion = '#' + dConfig.status.latestVersion
          let pod = queryPod(this.podList, bconfig.metadata.name, dConfig.status.latestVersion)
          if (pod) {
            data.dHistory = pod.status.phase
            data.dAge = timeSince(pod.status.startTime)
          } else {
            let controller = queryController(this.controllerList, bconfig.metadata.name, dConfig.status.latestVersion)
            if (controller) {
              data.dHistory = controller.metadata.annotations['openshift.io/deployment.phase']
              data.dAge = '-'
            }
          }
        }
        dataset.push(data)
      })
      return dataset
    }
  },
  mounted () {
    this.requestLists()
  },
  methods: {
    ...mapActions('project', ['setCurrentPackage']),
    ...mapActions('build', [
      'getBuildConfigList',
      'getBuildList',
      'deleteBuildConfig']),
    ...mapActions('deploy', [
      'getDeployConfigList',
      'deleteDeployConfig',
      'getDeployConfig',
      'editDeployConfig']),
    ...mapActions('pod', ['getPodList', 'deletePod']),
    ...mapActions('controller', [
      'getControllerList',
      'deleteReplicationController']),
    requestLists () {
      this.getBuildConfigList(this.projectName).then((list) => {
        this.getBuildList(this.projectName)
      })
      this.getDeployConfigList(this.projectName).then((list) => {
        this.getPodList(this.projectName)
        this.getControllerList(this.projectName)
      })
    },
    goLink (item) {
      this.setCurrentPackage(item.name)
      this.$router.push({
        name: 'detail',
        params: {
          projectName: this.projectName,
          packageName: item.name,
          lastBuild: item.bNumber,
          lastDeploy: item.dVersion
        }
      })
    },
    startPod (item) {
      this.getDeployConfig({
        projectName: this.projectName,
        buildName: item.name
      })
        .then(response => {
          this.editDeployConfig({
            projectName: this.projectName,
            buildName: item.name,
            paused: false,
            form: response
          })
            .catch(error => {
              this.$refs.packageError.show({
                title: this.$i18n.t('popup.startPodTitle'),
                text: error
              })
            })
        })
        .catch(error => {
          this.$refs.packageError.show({
            title: this.$i18n.t('popup.startPodTitle'),
            text: error
          })
        })
    },
    stopPod (item) {
      let pod = queryPod(this.podList, item.name, item.dVersion.slice(1))
      if (pod) {
        this.getDeployConfig({
          projectName: this.projectName,
          buildName: item.name
        }).then((deployconfig) => {
          this.editDeployConfig({
            projectName: this.projectName,
            buildName: item.name,
            paused: true,
            form: deployconfig
          }).then((response) => {
            this.deleteReplicationController({
              projectName: this.projectName,
              deployName: pod.metadata.labels.deployment
            })
            this.deletePod({
              projectName: this.projectName,
              podName: pod.metadata.name
            })
          })
            .catch(error => {
              this.$refs.packageError.show({
                title: this.$i18n.t('popup.stopPodTitle'),
                text: error
              })
            })
        })
          .catch(error => {
            this.$refs.packageError.show({
              title: this.$i18n.t('popup.stopPodTitle'),
              text: error
            })
          })
      } else {
        this.$refs.packageError.show({
          title: this.$i18n.t('popup.stopPodTitle'),
          text: this.$i18n.t('popup.startPodUnknownError')
        })
      }
    },
    deletePackage (item) {
      this.deleteBuildConfig({
        projectName: this.projectName,
        configName: item.name
      })
        .catch(error => {
          this.$refs.packageError.show({
            title: this.$i18n.t('popup.delPackageTitle'),
            text: error
          })
        })
      this.deleteDeployConfig({
        projectName: this.projectName,
        configName: item.name
      })
        .catch(error => {
          this.$refs.packageError.show({
            title: this.$i18n.t('popup.delPackageTitle'),
            text: error
          })
        })
    },
    imageReady (name) {
      // @TODO
      return true
    },
    getStatus (item) {
      switch (item.dHistory) {
        case 'Running':
        case 'Pending':
          return '구동중'
        case 'Succeeded':
        case 'Failed':
        case '-':
          if (item.bHistory === 'Complete' || this.imageReady(item.name)) {
            return '구동가능'
          } else {
            return '구동불가능'
          }
        default:
          return '구동불가능'
      }
    },
    onHighlight (field) {
      if (field && field.includes('Fail')) {
        return true
      }
      return false
    }
  },
  watch: {
    projectName: {
      immediate: true,
      handler: function () {
        this.requestLists()
      }
    },
    getDeployConfigList () {
      this.getPodList(this.projectName)
      this.getControllerList(this.projectName)
    }
  }
}
</script>

<style scoped>
</style>
